import axios from 'axios';

enum Level {
    INFO = 'info',
    DEBUG = 'debug',
    ERROR = 'error'
}

export default class LogApi {
  static logServerUrl = 'https://qsticker-api-test-cloud-run-5iz3p3mbtq-de.a.run.app/logging/'; // TODO: put a real log server here

  private static async base(message: string, level: Level): Promise<void> {
    LogApi.logServerUrl += `?message=${message}&severity=${level}`;
    await axios.get(LogApi.logServerUrl).catch(() => {
      //
    });
  }

  static async debug(message: string): Promise<void> {
    this.base(message, Level.DEBUG);
  }
}
