import Vue from 'vue';
import liff from '@line/liff';
import Notifications from 'vue-notification';
import VueCookies from 'vue-cookies';
import App from './App.vue';
import router from './router';
import store from './store';
import vuetify from './plugins/vuetify';
import UserApi from './apis/UserApi';
import i18n from './i18n';
import LogApi from './apis/LogApi';

Vue.config.productionTip = false;

Vue.use(VueCookies);
Vue.$cookies.config('7d');

Vue.use(Notifications);

// disable line login for modes other than production
if (process.env.NODE_ENV === 'production') {
  LogApi.debug('deployment number: 10');
  LogApi.debug(`full url: ${window.location.href}`);

  store.commit('setLoading', true);
  liff.init({ liffId: '1656074648-82kPY529' }).then(() => {
    LogApi.debug('inited');

    if (!liff.isInClient() && !liff.isLoggedIn()) {
      // log external browser & not logged in
      LogApi.debug('not in client && not logged in');

      // set Endpoint Url in line developer console, 互動貼圖登入 > LIFF > LIFF ID: 1656074648-82kPY529 > Endpoint URL
      const endpointUrl = 'https://qsticker.github.io/form';
      liff.login({
        redirectUri: `${endpointUrl}/#${router.currentRoute.fullPath}`,
      });
    }

    const userInfo = liff.getDecodedIDToken();
    if (userInfo && userInfo.sub) {
      store.commit('updateLineLoginInfo', userInfo);

      // TODO: handle 404 not found case, since user could visit this site before registering in our channel
      UserApi.getUserId(userInfo.sub).then((userId: string) => {
        store.commit('updateUserId', userId);
      });
    }
  }).catch(() => {
    //
  }).finally(() => {
    store.commit('setLoading', false);
  });
} else if (process.env.NODE_ENV === 'development') {
  if (process.env.VUE_APP_USER_ID) { store.commit('updateUserId', process.env.VUE_APP_USER_ID); }
}

export default new Vue({
  router,
  store,
  vuetify,
  i18n,
  render: (h) => h(App),
}).$mount('#app');
