






























import Vue from 'vue';
import UserApi from './apis/UserApi';
import { NotifyEventBus } from './libs/EventBus';

export default Vue.extend({
  name: 'App',

  data: () => ({
    //
  }),
  computed: {
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
  },
  mounted() {
    NotifyEventBus.$on('push', this.$notify);

    /**
     * Solve async data flow by polling
     *
     * UserApi.retrieveProfile need "userId" to work,
     * and "userId" is obtained by calling UserApi.getUserId, which is called async in main.ts.
     *
     * So we need either RxJs observer or simple and brute force polling solution
     * current solution is polling, which is not elegant but working.
     */
    const intervalId = setInterval(() => {
      if (this.$store.state.userId) {
        clearInterval(intervalId);
        UserApi.retrieveProfile().then((profile) => {
          if (profile && profile.language) {
            this.$i18n.locale = profile.language;
          }
        });
      }
    }, 50);
  },
});
