import Language from '@/models/LanguageModel';
import UserProfileModel from '@/models/UserProfileModel';
import store from '@/store';
import api from './BaseApi';

export default class UserApi {
  static async retrieveProfile(): Promise<UserProfileModel> {
    const result = await api.get(`user/${store.state.userId}/profile`);
    return result.data;
  }

  static async updateProfile(name: string, businessDefinedId: string, language: Language): Promise<void> {
    const body = {
      name,
      businessDefinedId,
      language,
    };

    if (store.state.userId) {
      const result = await api.put(`user/${store.state.userId}/profile`, body);
      return result.data;
    }
    return Promise.reject(new Error('userId not found, failed to update profile'));
  }

  static async getUserId(internalId: string): Promise<string> {
    const result = await api.get(`user/line/${internalId}`);
    return result.data.userId;
  }
}
