import { LineLoginInfoModel } from '@/models/LineLoginInfoModel';
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    lineLoginInfo: null as null | LineLoginInfoModel,
    userId: null as null | string,
    isLoading: false,
    memberGroupCode: null as null | string,
  },
  mutations: {
    updateLineLoginInfo(state, payload) {
      state.lineLoginInfo = payload;
    },
    updateUserId(state, id) {
      state.userId = id;
    },
    setLoading(state, value) {
      state.isLoading = value;
    },
    updateMemberGroupCode(state, string) {
      state.memberGroupCode = string;
    },
  },
  actions: {
  },
  modules: {
  },
});
